@font-face{
    font-family:SF Pro Display;
    src:local("SF Pro Display"),
    url('../../assets/fonts/apple/SFPRODISPLAYREGULAR.OTF') 
    format("truetype")
}
@font-face{
    font-family:SF Pro Display Bold;
    src:local("SF Pro Display Bold"),
    url('../../assets/fonts/apple/SFPRODISPLAYBOLD.OTF') 
    format("truetype")
}

body{
    font-family:SF Pro Display;
    color: #494949;
}

section{
    width: 100%;
}

.menu_superior:first-child {
    margin-top: -8px;
}

.menu_superior {
    background-color: #333;
}

.menu_secundario, .menu_superior {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.menu_secundario span{
    color:#494949!important;
    font-family:SF Pro Display Bold;
    font-size:1.1rem;
    padding-left: 10px;
}

.appleIntro, .content {
    display: flex;
    justify-content: center;
}

.menu_secundario{
    border-bottom:1px solid #ccc
}

.content {
    align-items: center;
    flex-direction: column;
}

h1 {
    font-size: 24px;
    line-height: 1.16667;
    font-weight: 600;
    letter-spacing: .009em;
    color: #494949;
    margin-top: 20px;
}

.content span {
    color: #494949!important;
    font-size: 17px;
    margin: 20px 0;
}

.input_email {
    border: 1px solid #d2d2d7;
    border-radius: 12px;
    font-size: 1em;
    height: 50px;
    padding: 0 16px;
    width: 270px;
}

.input_senha {
    border: 1px solid #d2d2d7;
    border-radius: 0 0 12px 12px;
    font-size: 1em;
    height: 50px;
    margin-top: -6px;
    padding: 0 16px;
    width: 270px;
}

.input_email:focus,
.input_senha:focus{
    outline: none !important;
    border-color: #0071e3;
    box-shadow: 0 0 0 4px rgb(0 125 250 / 60%);
}

.btn_enter {
    background: none;
    border: none;
    margin-left: 250px;
    margin-top: -42px;
    position: relative;
}

.lembrar {
    color: #494949;
    margin: 50px;
}

.pop-container {
    display: none;
}


.pop-container .pop-bottom {
    background-color: #fae9a3;
    border-radius: 5px;
    border: 1px solid rgba(185,149,1,.47);
    box-shadow: 0 5px 10px 2px rgb(0 0 0 / 10%);
    padding: 11px;
    margin-top: -106px;
    color: #503e30 !important;
    text-align: center;
    width: 280px;
}

a {
    color: #06c;
    letter-spacing: inherit;
    color: var(--mdc-theme-primary);
    line-height: 1.4287;
    text-decoration: none;
    line-height: 1.42859;
    font-weight: 400;
}

.pop-bottom a{
    text-decoration: underline;
    color: #503e30 !important;
}

.parte2{
    display: none;
}

