a{
    text-decoration: none;
    color: var(--mdc-theme-primary); 
}

b{
    font-weight: 600;
}

.mdc-text-field{
    margin-top: 40px;
    margin-bottom: 15px;
    opacity: 0.7;
}

.mdc-text-field--focused:not(.mdc-text-field--disabled)
.mdc-floating-label {
    color: var(--mdc-theme-primary);
}

.mdc-button{
    text-transform: none;
    font-weight: bold;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    font-size: .875rem;
    letter-spacing: .0107142857em;
    font-weight: 500;
    text-transform: none;
}

.mdc-button--raised{
    padding: 15px 30px;
}

/* 
Error class:

mdc-text-field--invalid

*/

.esqueceu{
    font-size: 14px !important;
    border-radius: 4px;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    display: inline-block;
    font-weight: 500;
    letter-spacing: .25px;
    outline: 0;
    position: relative;
    background-color: transparent;
    cursor: pointer;
    font-size: inherit;
    padding: 0;
    text-align: left;
    border: 0;
}

.submsg {
    color: #5f6368;
    font-size: 14px;
    line-height: 1.4286;
    margin-top: 32px;
    padding-bottom: 3px;
    padding-top: 9px;
}

.erro svg{
    margin-right: 5px;
}

.idioma{
    width: 200px;
    display: flex;
    justify-content: space-between;
}

.idioma span{
    color: black;
    font-size: 1.5em;
}

@media (max-width: 600px) {
    .idioma{
        width: 100%;
    }
}

/* Part 2 */
.email_circle{
    display: flex;
    align-items: center;
    border: 1px solid #dadce0;
    border-radius: 25px;
    cursor: pointer;
    line-height: normal!important;
    margin: 7px auto;
    padding: 3px;
    font-size: small;
}

.show_checkbox{
    margin-top: -10px;
}

.part2{
    display: none;
}