.form_background{
    background-color: #f0ebf8;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.card_quiz{
    margin: 10px 6%;
    padding: 20px;
    font-family: 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card_top{
    border-top: solid 8px rgb(103, 58, 183);
}

.line{
    display: flex;
    align-items: center;
    width: 100%;
}

.mdc-radio .mdc-radio__native-control:enabled+.mdc-radio__background .mdc-radio__inner-circle {
    border-color: rgb(103, 58, 183) !important;
}

.mdc-radio .mdc-radio__native-control:enabled:checked+.mdc-radio__background .mdc-radio__outer-circle {
    border-color: rgb(103, 58, 183) !important;
}

.send_button,
.auth{
    margin: 10px 6%; 
    text-align: center;
}

.send_button:not(:disabled) {
    background-color: #6200ee;
}

.validation_number small{
    margin-top: 32px;
    display: block;
}

.gcode2{
    margin: 0 auto;
    width: max-content;
    margin-top: 30px;
}
.gcode2_circle{
    background-color: grey;
    color: #282828;
    padding: 20px;
    border-radius: 50%;
}

.sim_sou{
    background-color: white;
    padding: 2px 5px;
    border: solid 1px #a0a0a0;
    border-radius: 3px;
    line-height: 30px;
    color: var(--mdc-theme-primary);
}

.google_code1{
    color: var(--mdc-theme-primary);
    font-size: 1.2em;
}

.confirm_code{
    width: 100%;
}

.text-danger,
.text-danger a{
    color: red;
}

.text-danger a{
    font-weight: bold;
    text-decoration: underline;
}

.validation_number,
.validation_code,
.validation_wrong,
.answer{
    display: none;
}