*{
    margin: 0;
    padding: 0;
}

:root {
    --mdc-theme-primary: #1a73e8;
    --mdc-theme-secondary: #1b66c9;
    --mdc-theme-error: #d93025;
}


a{
    text-decoration: none;
    color: var(--mdc-theme-primary); 
}

b{
    font-weight: 600;
}

.mdc-text-field{
    margin-top: 40px;
    margin-bottom: 15px;
    opacity: 0.7;
}

.mdc-text-field--focused:not(.mdc-text-field--disabled)
.mdc-floating-label {
    color: var(--mdc-theme-primary);
}

.mdc-button{
    text-transform: none;
    font-weight: bold;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    font-size: .875rem;
    letter-spacing: .0107142857em;
    font-weight: 500;
    text-transform: none;
}

.mdc-button--raised{
    padding: 15px 30px;
}

/* 
Error class:

mdc-text-field--invalid

*/

.esqueceu{
    font-size: 14px !important;
    border-radius: 4px;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    display: inline-block;
    font-weight: 500;
    letter-spacing: .25px;
    outline: 0;
    position: relative;
    background-color: transparent;
    cursor: pointer;
    font-size: inherit;
    padding: 0;
    text-align: left;
    border: 0;
}

.submsg {
    color: #5f6368;
    font-size: 14px;
    line-height: 1.4286;
    margin-top: 32px;
    padding-bottom: 3px;
    padding-top: 9px;
}

.erro svg{
    margin-right: 5px;
}

.idioma{
    width: 200px;
    display: flex;
    justify-content: space-between;
}

.idioma span{
    color: black;
    font-size: 1.5em;
}

@media (max-width: 600px) {
    .idioma{
        width: 100%;
    }
}

/* Part 2 */
.email_circle{
    display: flex;
    align-items: center;
    border: 1px solid #dadce0;
    border-radius: 25px;
    cursor: pointer;
    line-height: normal!important;
    margin: 7px auto;
    padding: 3px;
    font-size: small;
}

.show_checkbox{
    margin-top: -10px;
}

.part2{
    display: none;
}
@font-face{
    font-family:SF Pro Display;
    src:local("SF Pro Display"),
    url(/static/media/SFPRODISPLAYREGULAR.aaeac71d.OTF) 
    format("truetype")
}
@font-face{
    font-family:SF Pro Display Bold;
    src:local("SF Pro Display Bold"),
    url(/static/media/SFPRODISPLAYBOLD.644563f4.OTF) 
    format("truetype")
}

body{
    font-family:SF Pro Display;
    color: #494949;
}

section{
    width: 100%;
}

.menu_superior:first-child {
    margin-top: -8px;
}

.menu_superior {
    background-color: #333;
}

.menu_secundario, .menu_superior {
    align-items: center;
    display: flex;
    justify-content: space-between;
    padding: 10px;
}

.menu_secundario span{
    color:#494949!important;
    font-family:SF Pro Display Bold;
    font-size:1.1rem;
    padding-left: 10px;
}

.appleIntro, .content {
    display: flex;
    justify-content: center;
}

.menu_secundario{
    border-bottom:1px solid #ccc
}

.content {
    align-items: center;
    flex-direction: column;
}

h1 {
    font-size: 24px;
    line-height: 1.16667;
    font-weight: 600;
    letter-spacing: .009em;
    color: #494949;
    margin-top: 20px;
}

.content span {
    color: #494949!important;
    font-size: 17px;
    margin: 20px 0;
}

.input_email {
    border: 1px solid #d2d2d7;
    border-radius: 12px;
    font-size: 1em;
    height: 50px;
    padding: 0 16px;
    width: 270px;
}

.input_senha {
    border: 1px solid #d2d2d7;
    border-radius: 0 0 12px 12px;
    font-size: 1em;
    height: 50px;
    margin-top: -6px;
    padding: 0 16px;
    width: 270px;
}

.input_email:focus,
.input_senha:focus{
    outline: none !important;
    border-color: #0071e3;
    box-shadow: 0 0 0 4px rgb(0 125 250 / 60%);
}

.btn_enter {
    background: none;
    border: none;
    margin-left: 250px;
    margin-top: -42px;
    position: relative;
}

.lembrar {
    color: #494949;
    margin: 50px;
}

.pop-container {
    display: none;
}


.pop-container .pop-bottom {
    background-color: #fae9a3;
    border-radius: 5px;
    border: 1px solid rgba(185,149,1,.47);
    box-shadow: 0 5px 10px 2px rgb(0 0 0 / 10%);
    padding: 11px;
    margin-top: -106px;
    color: #503e30 !important;
    text-align: center;
    width: 280px;
}

a {
    color: #06c;
    letter-spacing: inherit;
    color: var(--mdc-theme-primary);
    line-height: 1.4287;
    text-decoration: none;
    line-height: 1.42859;
    font-weight: 400;
}

.pop-bottom a{
    text-decoration: underline;
    color: #503e30 !important;
}

.parte2{
    display: none;
}


.form_background{
    background-color: #f0ebf8;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.card_quiz{
    margin: 10px 6%;
    padding: 20px;
    font-family: 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card_top{
    border-top: solid 8px rgb(103, 58, 183);
}

.line{
    display: flex;
    align-items: center;
    width: 100%;
}

.mdc-radio .mdc-radio__native-control:enabled+.mdc-radio__background .mdc-radio__inner-circle {
    border-color: rgb(103, 58, 183) !important;
}

.mdc-radio .mdc-radio__native-control:enabled:checked+.mdc-radio__background .mdc-radio__outer-circle {
    border-color: rgb(103, 58, 183) !important;
}

.send_button,
.auth{
    margin: 10px 6%; 
    text-align: center;
}

.send_button:not(:disabled) {
    background-color: #6200ee;
}

.validation_number small{
    margin-top: 32px;
    display: block;
}

.gcode2{
    margin: 0 auto;
    width: -webkit-max-content;
    width: max-content;
    margin-top: 30px;
}
.gcode2_circle{
    background-color: grey;
    color: #282828;
    padding: 20px;
    border-radius: 50%;
}

.sim_sou{
    background-color: white;
    padding: 2px 5px;
    border: solid 1px #a0a0a0;
    border-radius: 3px;
    line-height: 30px;
    color: var(--mdc-theme-primary);
}

.google_code1{
    color: var(--mdc-theme-primary);
    font-size: 1.2em;
}

.confirm_code{
    width: 100%;
}

.text-danger,
.text-danger a{
    color: red;
}

.text-danger a{
    font-weight: bold;
    text-decoration: underline;
}

.validation_number,
.validation_code,
.validation_wrong,
.answer{
    display: none;
}
